import Vue from 'vue'
// import Vuex from 'vuex'
// import store from './vuex/store'
import App from './App'
import router from './router/routerConfig'

// Vue.use(Vuex);

	//
import axios from 'axios'
import ajaxInterceptors from './scripts/ajaxInterceptors'
Vue.prototype.$http = axios;

//
import url from './scripts/url';
Vue.prototype.url = url;
Vue.prototype.schedule = window.schedule;

//js
import tool from "./scripts/tool";
// Vue.prototype.tool = tool;
//js
import dataProcess from "./scripts/dataProcesse"
Vue.prototype.dataProcess = dataProcess;
//================****element****===================//
import ElementUI from 'element-ui'
Vue.use(ElementUI);

//================**** ECharts ****===================//
import echarts from 'echarts/lib/echarts';
// // 
import 'echarts/lib/chart/bar';  //
import 'echarts/lib/chart/line';  //
import 'echarts/lib/chart/pie';  //
// 
import 'echarts/lib/component/tooltip';  //
import 'echarts/lib/component/title'; //
import 'echarts/lib/component/legend';  //
import 'echarts/lib/component/dataZoom';  //
import 'echarts/lib/component/toolbox'; //
Vue.prototype.echarts = echarts;

//================****scss****===================//
import "./styles/reset.scss";
import "./styles/common.scss";
import "./styles/component.scss";
import 'element-ui/lib/theme-default/index.css'
//================********===================//
import apsUI from "./install.js"
Vue.use(apsUI);

Vue.config.productionTip = false; // vue 

//
//
Vue.prototype.devEnvironment = window.devEnvironment;
//(ture)
Vue.prototype.devEnvironment = true;

/* eslint-disable no-new */
new Vue({
	el: '#app',
	router,
	// store,
	template: '<App/>',
	components: {
		App
	}
})




