/** * Created by linzx on 2017/5/31. */
<template>
    <div class="right-content-box prediction-oee">
        <aps-query-condition-box>
            <add-scheme :allSchemeList="allSchemeList" :value="schemeIdList" @change="aaa()">
                <label class="ml-20 input-checkbox" for="hadSavedScheduleResult">
                    <input
                      id="hadSavedScheduleResult"
                      type="checkbox"
                      v-model="hadSavedScheduleResult">
                    <span class="checkBox-inner"></span>
                    
                </label>
            </add-scheme>
            <p class="mt-5 mb-10 flex">
                 
                <label class="input-radio" for="equipment">
                  <input
                    id="equipment"
                    name="equipment"
                    type="radio"
                    value="equipment"
                    v-model="lookDimension">
                  <span></span>
                  
                </label>
            </p>
            <date-select v-model='date'></date-select>
            <div class="query-conditions-list">
                <location-cascader class="mt-20 mr-10"
                    :writelocation="schemeIdList"
                    v-model="selectLocationList">
                </location-cascader>
                <div class="query-conditions mt-20 mr-10">
                    <span>  </span>
                    <aps-dropdown
                        v-model="equipmentTypeValue"
                        multiple>
                        <aps-option
                            v-for="item in equipmentTypeList"
                            :value="item.modelId"
                            :label="item.modelName">
                        </aps-option>
                    </aps-dropdown>
                </div>
                <div class="query-conditions mt-20">
                    <span>&#12288;&#12288;  </span>
                    <aps-dropdown
                        v-model="equipmentValue"
                        multiple>
                        <aps-option
                            v-for="item in equipmentList"
                            :value="item.productUnitId"
                            :label="item.productUnitName">
                        </aps-option>
                    </aps-dropdown>
                </div>
                <a  class="default-btn mt-20"
                    href="javascript:void(0);"
                    @click="searchOee()">
                    
                </a>
            </div>
        </aps-query-condition-box>
        <p class="c-theme-color" v-show="!showTable">{{showTips}}</p>
        <div style="height: 30px;">
            <a
                v-show="isShowReturnFirst"
                @click="returnFirstPageFromSecond"
                class="default-btn return-first"
                href="javascript:void(0);">
                
            </a>
        </div>
        <div id="canvas" class="canvas" v-show="showTable"></div>
        <div class="aps-table predict-table jPredictTable" v-show="showTable">
            <table
                v-show="showTable">
                <thead>
                    <tr>
                        <th></th>
                        <th
                            v-for="equipmentName in schemeCompareTable.equipmentNameList">
                            {{equipmentName.value}}
                        </th>
                    </tr>
                </thead>
                <tbody>
                    <tr
                        v-for="row in schemeCompareTable.equipmentValueList">
                        <td
                            v-for="data in row"
                            :title="data">
                            {{data}}
                        </td>
                    </tr>
                </tbody>
            </table>
        </div>
    </div>
</template>

<script>
	/*type="text/ecmascript-6"*/

	export default {
      data() {
          return {
              showTips : '',
              myCharts: '',
              hadSavedScheduleResult: false, //
              lookDimension: 'equipment', //
              allSchemeList: [], //
              schemeIdList: [], //idList
              checked: false,
              quickTime: "", // 
              minTime : new Date(), // 
              //option
              startTime: new Date().getTime(), //
              endTime: new Date().getTime() + 86400000, //
              date: {
                  startTime: +new Date(),
                  endTime: +new Date() + 86400000,
                  quickSelect : ['currentWeek','nextWeek','currentMonth','nextMonth'],
                  pickerOptions : {
                      disabledDate(time) {
                          return (time.getTime() < new Date() - 86400000) || (time.getTime() > new Date() - 2*86400000);
                      }
                  },
                  pickerEndOptions : {
                      disabledDate(time) {
                          return time.getTime() < new Date() - 86400000;
                      }
                  }
              },
              selectLocationList: [], //
              equipmentTypeList: [], //
              equipmentTypeValue: "", //
              equipmentList: [], //
              equipmentValue: "", //
              writelocation: '',
              allSchemeCompareData: {
                  horizontalAxisData: {},
                  longitudinalAxisData: []
              }, //
              schemeCompareTable: {}, //
              goSecondPageParams: "", //url
              isShowReturnFirst: false, //
              showTable: false, //table
          }
      },
      watch: {
          //
          hadSavedScheduleResult(newValue, oldValue) {
          	  //
          	  if(this.isShowReturnFirst){
          	  	  if(newValue){
          	  	  	  this.$http.get(this.url.result_equipmentList_by_equipmentType + this.goSecondPageParams).then((res) => {
          	  	  	  	  res.data.longitudinalAxisData[0].schemeName = "";
                          this.allSchemeCompareData.longitudinalAxisData = this.allSchemeCompareData.longitudinalAxisData.concat(res.data.longitudinalAxisData);
                          this.packageECharts();
                      })
                  }else{
                      this.$http.get(this.url.temp_equipmentList_by_equipmentType + `?schemeIdList=${this.schemeIdList.join()}&startTime=${t.getCorrectDate(this.date.startTime)}&endTime=${t.getCorrectDate(this.date.endTime)}&equipmentTypeIdList=${this.equipmentTypeValue.join()}&equipmentIdAndTypeList=${this.equipmentValue}&dimensionTypeEnum=EQUIPMENTTYPE&isCalculateUnusedEquip=1&locationIdList=${this.selectLocationList.join()}`).then((res) => {
                          this.allSchemeCompareData.longitudinalAxisData.pop();
                          this.packageECharts();
                          this.showTable = this.allSchemeCompareData.longitudinalAxisData.length !== 0;
                      })
                  }
              }else{
                  if(newValue){
                      this.getHadSavedSchedule();//
                  }else{
                      /*
                       * ,
                       *   ''concat,
                       */
                      //
                      this.allSchemeCompareData.longitudinalAxisData.pop();
                      this.packageECharts();
                      //
                      this.showTable = this.allSchemeCompareData.longitudinalAxisData.length !== 0;
                      this.showTips = "";
                  }
              }
          },
          'startTime': {
              handler: function(val, oldVal) {
                  this.getAllEquipment();
              },
          },
          'endTime': {
              handler: function(val, oldVal) {
                  this.getAllEquipment();
              },
          },
          'selectLocationList': {
              handler: function(val, oldVal) {
                  this.getAllEquipment();
              },
          },
          equipmentTypeValue(){
              this.getAllEquipment();
          },
          showSchemeList(){
          	  //56
          	  this.isShowAddPlan = this.showSchemeList.length > 0 && this.showSchemeList.length < 5;
          },
      },
      mounted() {
          this.$nextTick(() => {
              //dom
              this.myCharts = this.echarts.init(document.getElementById('canvas'));
          });
          //
          this.$http.get(this.url.test_writable).then((res) => {
              this.allSchemeList = res.data;
              //url
              let schemeId = t.getUrlParams('schemeId');
          });
          //
          this.$http.get(this.url.min_scheme_time).then((res) => {
              this.minTime = res.data;
          });
          //
          this.getAllEquipmentType();
          this.getAllEquipment();
          let _this = this;
          window.onresize = function(){
              setTimeout(_this.myCharts.resize,100);
          }
      },
      methods: {
      	  aaa(v){console.log(v)},
          //
          getHadSavedSchedule(){
              const url = this.url.result_equipment_oee + `?startTime=${t.getCorrectDate(this.date.startTime)}&endTime=${t.getCorrectDate(this.date.endTime)}&equipmentTypeIdList=${this.equipmentTypeValue}&equipmentIdAndTypeList=${this.equipmentValue}&dimensionTypeEnum=EQUIPMENTTYPE&isCalculateUnusedEquip=1&locationIdList=${this.selectLocationList.join()}`;
              this.$http.get(url).then((res) => {
                  //
                  res.data.longitudinalAxisData[0].schemeName = '';
                  //y
                  if (t.isEmptyObject(this.allSchemeCompareData.horizontalAxisData)) {
                      this.allSchemeCompareData = res.data;
                  } else {
                      this.allSchemeCompareData.longitudinalAxisData = this.allSchemeCompareData.longitudinalAxisData.concat(res.data.longitudinalAxisData);
                  }
                  this.packageECharts();
              })
          },
          //
          getAllEquipmentType() {
              const url = this.url.get_all_equipmentType;
              this.$http.get(url).then((res) => {
                    this.equipmentTypeList = res.data;
                    this.equipmentTypeList.map((item) => {
                        item.modelId = item.modelId + "_" + item.modelType;
                    })
              });
          },
          //
          getAllEquipment() {
              let cacheTypeStr = [];
          	  if(!this.equipmentTypeValue){
                  this.equipmentTypeValue = "";
              }else{
          	  	  //
          	  	  if(!Array.isArray(this.equipmentTypeValue)){
                      this.equipmentTypeValue = this.equipmentTypeValue.split(",");
                  }
          	  }
          	  const data = {
                  startTime : t.getCorrectDate(this.date.startTime),
                  endTime : t.getCorrectDate(this.date.endTime),
                  searchType : 1,
                  modelIdList : this.equipmentTypeValue,
                  locationFilterList : this.selectLocationList
              };
          	  this.$http.post(this.url.get_all_equipment,data).then(res => {
                  //equipmentList
                  this.equipmentList = this.dataProcess.processEquipmentList(res.data);
              })
          },
          //
          packageECharts(){
              //=========
              let equipmentList = processEchartsData(this.allSchemeCompareData);
              this.myCharts.clear();//echarts
              equipmentList.title = {text : ""};//
              this.myCharts.setOption(this.dataProcess.getBarEChartsOption(equipmentList));
              //=========
              this.schemeCompareTable = this.dataProcess.showTableData(this.allSchemeCompareData);
              this.showTable = true;

              //
              this.myCharts.on('click', (params) => {
                  let equipmentList = processEchartsData(this.allSchemeCompareData);
                  //
                  if (this.isShowReturnFirst) {
                      return;
                  }
                  //ID
                  const index = params.dataIndex,//
                      clickEChartsType = params.componentSubType,//
                      equipmentType = equipmentList.xAxisList[index].key.split(":").reverse().join("_");//
                  //
                  //key 0:001 equipmentTypeValue 001_1(id_type)
                  this.equipmentTypeValue = [equipmentType.split(":").reverse().join("_")];
                  //2017-7-8
                  this.goSecondPageParams = `?startTime=${t.getCorrectDate(this.date.startTime)}&endTime=${t.getCorrectDate(this.date.endTime)}&equipmentTypeIdList=${this.equipmentTypeValue.join(",")}&dimensionTypeEnum=EQUIPMENTTYPE&isCalculateUnusedEquip=1&locationIdList=${this.selectLocationList.join()}`;
                  this.searchEquipmentOee(this.equipmentTypeValue,clickEChartsType);
              });
              setTimeout(this.myCharts.resize,0);
          },
          //
          searchOee(){
              //
              if((this.date.startTime + 86400000) < new Date()){
                  this.$message.error('');
                  return;
              }
              //
              if(t.getCorrectDate(this.startTime) > t.getCorrectDate(this.endTime)){
                  this.$message.error('');
                  return;
              }
              //true
              if(this.isShowReturnFirst){
                  this.searchEquipmentOee();
              }else{
                  this.searchEquipmentTypeOee();
              }
          },
          /**
           * desc:oee
           * time: 2017-06-28
           * last:linzx
           * @param: pageUrl :
           **/
          searchEquipmentTypeOee() {
          	  console.log(this.schemeIdList);
              //,
              if (this.schemeIdList.length === 0) {
                  if(this.hadSavedScheduleResult){
                  	  //
                      this.allSchemeCompareData = {horizontalAxisData: {}, longitudinalAxisData: []};
                      this.getHadSavedSchedule();
                      return;
                  }else{
                      this.$message.error('');
                      return;
                  }
              }
              const PageUrlParams = `schemeIdList=${this.schemeIdList.join()}&startTime=${t.getCorrectDate(this.date.startTime)}&endTime=${t.getCorrectDate(this.date.endTime)}&equipmentTypeIdList=${this.equipmentTypeValue}&equipmentIdAndTypeList=${this.equipmentValue}&dimensionTypeEnum=EQUIPMENTTYPE&isCalculateUnusedEquip=1&locationIdList=${this.selectLocationList.join()}`;
              const firstPageUrl = this.url.temp_equipment_oee + '?' + PageUrlParams;
              this.$http.get(firstPageUrl).then((res) => {
                  this.allSchemeCompareData = res.data;
              }).then(() => {
                  if(this.hadSavedScheduleResult){
                      this.getHadSavedSchedule();//,
                  }else{
                      this.packageECharts();
                  }
              })
          },
          /**
           * desc:
           * time: 2017-07-08
           * last:linzx
           * @params :equipmentType : 
           * @params :clickEChartsType 
           **/
          searchEquipmentOee(equipmentType = this.equipmentTypeValue,clickEChartsType = 'bar'){
              let secondPageUrl = this.url.temp_equipmentList_by_equipmentType + `?schemeIdList=${this.schemeIdList.join()}&startTime=${t.getCorrectDate(this.date.startTime)}&endTime=${t.getCorrectDate(this.date.endTime)}&equipmentTypeIdList=${equipmentType}&equipmentIdAndTypeList=${this.equipmentValue}&dimensionTypeEnum=EQUIPMENTTYPE&isCalculateUnusedEquip=1&locationIdList=${this.selectLocationList.join()}`;
              let secondPageCacheUrl = this.url.result_equipmentList_by_equipmentType + `?startTime=${t.getCorrectDate(this.date.startTime)}&endTime=${t.getCorrectDate(this.date.endTime)}&equipmentTypeIdList=${equipmentType}&equipmentIdAndTypeList=${this.equipmentValue}&dimensionTypeEnum=EQUIPMENTTYPE&isCalculateUnusedEquip=1&locationIdList=${this.selectLocationList.join()}`;
              //url
              if(this.schemeIdList.length > 0){
                  this.$http.get(secondPageUrl).then((res) => {
                      this.allSchemeCompareData = res.data;
                  }).then(() =>{
                      if(this.hadSavedScheduleResult){
                          this.$http.get(secondPageCacheUrl).then((res) => {
                              //
                              res.data.longitudinalAxisData[0].schemeName = '';
                              //
                              this.allSchemeCompareData.longitudinalAxisData = this.allSchemeCompareData.longitudinalAxisData.concat(res.data.longitudinalAxisData);
                              //echarts
                              const equipmentList = processEchartsData(this.allSchemeCompareData);
                              equipmentList.type = clickEChartsType;//
                              equipmentList.title = {text : ""};//
                              this.myCharts.setOption(this.dataProcess.getBarEChartsOption(equipmentList));
                              //
                              this.schemeCompareTable = this.dataProcess.showTableData(this.allSchemeCompareData);
                          })
                      }else{
                          let equipmentList = processEchartsData(this.allSchemeCompareData);
                          equipmentList.type = clickEChartsType;
                          equipmentList.title = {text : ""};//
                          this.myCharts.setOption(this.dataProcess.getBarEChartsOption(equipmentList));
                          //
                          this.schemeCompareTable = this.dataProcess.showTableData(this.allSchemeCompareData);
                      }
                  });
              }else{
//                  if(this.hadSavedScheduleResult) {
                      this.$http.get(secondPageCacheUrl).then((res) => {
                          this.allSchemeCompareData = res.data;
                          const equipmentList = processEchartsData(this.allSchemeCompareData);
                          equipmentList.type = clickEChartsType;//
                          equipmentList.title = {text : ""};//
                          this.myCharts.setOption(this.dataProcess.getBarEChartsOption(equipmentList));
                          //
                          this.schemeCompareTable = this.dataProcess.showTableData(this.allSchemeCompareData);
                      })
//                  }
              }
              //
              this.isShowReturnFirst = true;
          },
          //
          returnFirstPageFromSecond() {
          	  //
              this.equipmentTypeValue = [];
          	  //
              if(this.schemeIdList.length !== 0){
                  this.searchEquipmentTypeOee();
              }else{
                  this.allSchemeCompareData =  {
                      horizontalAxisData: {},
                      longitudinalAxisData: []
                  };
                  this.getHadSavedSchedule();
              }
              this.isShowReturnFirst = false;//
          }
      }
	}


	/**
	 * desc :  xy
	 * time: 2017-06-21
	 * last:linzx
	 * @param: resData  
	 * @return: Object schemeTableData echarts
	 **/
	function processEchartsData(resData) {
      const horizontalAxisData = resData.horizontalAxisData,
          longitudinalAxisData = resData.longitudinalAxisData;
        //
      const schemeTableData = {
          xAxisList: [],
          yAxisList: []
      };
      horizontalAxisData.forEach((item) => {
          const key = item.split("_")[0];
          const value = item.replace(key + "_","");
          const obj = {
              key : key,
              value : value,
          };
          schemeTableData.xAxisList.push(obj);
      });
      //y
      longitudinalAxisData.forEach((item) => {
          const valueList = [];
          schemeTableData.xAxisList.forEach((equipmentName) => {
              if(item.oeeMap[equipmentName.key] === undefined){
                  item.oeeMap[equipmentName.key] = 0;
              }
              valueList.push(item.oeeMap[equipmentName.key].toFixed(3));
          });
          schemeTableData.yAxisList.push({
              name: item.schemeName,
//              schemeId: item.schemeId,
              valueList: valueList
          })
      });
      return schemeTableData
	}
</script>
<style rel="stylesheet/scss" lang="scss" scoped>
    .predict-table{
        max-height:200px;
        overflow: auto;
        table{
            tbody{
                max-height: 200px;
                overflow: auto;
            }
        }
    }
    .canvas{
        margin: -30px auto 0;
        width:100%;
        height: 400px
    }
    .return-first{
        position: relative;
        z-index: 100;
    }
</style>



